@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
body {
  background-color: #0791e8;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

@media (min-width: 992px) {
    .menu {
      display: none !important;
    }
  }

.right {
  float:right;
    width:33.33%;
  text-align: right;
}

@media (min-width: 767px) {
    .menu {
      left: -25px
    }
  }

@media (max-width: 768px) {    
  .menu {
    left: -40px
  }
}

.menu {
    float: left;
    top: 17px;
}
@media (max-width: 767px) {
    .content {
        padding: 6px;
    }
    .background {
        padding: 6px;
    }
  }

@media (min-width: 767px) {
    .content {
        padding: 24px;
    }
    .background {
        padding: 19px;
        padding-left: 2px !important;
        overflow: initial;
    }
}


@media (min-width: 992px) {
    .pullright {
        margin-left: 250px;
    }
  }

  .pullright {
    margin-top: 64px;
}



.background {
    min-height: calc(100vh - 64px)
}

.content {
    min-height: 280px;
    background: #fff;
}

  
 .fixedheader {
     position: static;
 }


.login-page {
    width: 360px;
    padding: 8% 0 0;
    margin: auto;
}

.FormLogin {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 360px;
    margin: 0 auto 100px;
    padding: 45px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.textInfo {
    font-size: 12px;
}
.textInfo2{
    font-size: 12px;
    color: #FFFFFF!important;
}

.InputForm {
    width: 100%;
}
.FormLogin Button {
    width: 100%;
}
.FormLogin .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
}
.FormLogin .message a {
    color: #000000;
    text-decoration: none;
}

.bodyLoginPage {
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
}

.loading-page {
    width: 380px;
    padding: 15% 0 0;
    margin: auto;
}

.FormLoadingLogin {
    position: relative;
    z-index: 1;
    background: #FFFFFF;
    max-width: 380px;
    margin: 0 auto 100px;
    padding: 75px;
    text-align: center;
    box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}

.textInfo {
    font-size: 12px;
}
.textInfo2{
    font-size: 12px;
    color: #FFFFFF!important;
}

.InputForm {
    width: 100%;
}
.FormLoadingLogin Button {
    width: 100%;
}
.FormLoadingLogin .message {
    margin: 15px 0 0;
    color: #b3b3b3;
    font-size: 12px;
}
.FormLoadingLogin .message a {
    color: #000000;
    text-decoration: none;
}

.bodyLoadingLoginPage {
    background-image: url("https://i1.wp.com/visaoagro.com.br/wp-content/uploads/2020/10/floresta-amazonia-2019.jpg");
    font-family: "Roboto", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
}

